.paginations-page {
    width: 74px;
    height: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.paginations-page__box {
    width: 32px;
    height: 32px;
    background: #FFFFFF;
    box-shadow: 0px 7px 64px rgba(0, 0, 0, 0.07);
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    line-height: 20px;
    font-size: 15px;
    color: #6979F8;
    cursor: pointer;
}